import { Component, Input, Optional } from '@angular/core';
import { NotificationsCommunicationService } from '../../../notifications-common-ang/src/services/notifications-communication.service';
import { titles } from '../../../notifications-common-ang/src/config/titles.config';
import { NotificationRouting } from '../../../notifications-common-ang/src/models/notification-routing';

@Component({
  selector: 'bb-notifications-popups',
  templateUrl: './notifications-popups.component.html',
})
export class NotificationsPopupsComponent {
  /**
   * Comma-separated list of notifications origins to process (empty for all).
   */
  @Input() origins: string | undefined;

  /**
   * Comma-separated list of notifications severity levels to process (empty for all).
   */
  @Input() severityLevels: string | undefined;

  /**
   * Polling interval (in seconds).
   */
  @Input() pollingInterval: number | undefined;

  /**
   * String of allowed routes for navigation (conversation-view, arrangement-view, transaction-view, party-view, etc.).
   */
  @Input() allowedRoutes: string | undefined;

  /**
   * Auto-focus.
   */
  @Input() autofocus: boolean | undefined;

  /**
   * Display See More button.
   */
  @Input() displayShowMoreButtonInPopup: boolean | undefined;

  /**
   * Number of displayed lines in notification message.
   */
  @Input() popupMessageLineAmount: number | undefined;

  constructor(@Optional() private communicationService?: NotificationsCommunicationService) {}

  /**
   * Method that emits navigation data and triggers navigation.
   */
  navigate(navigationData: NotificationRouting) {
    if (this.communicationService) {
      this.communicationService.notificationNavigation(navigationData);
    } else {
      this.showNoCommunicationServiceProvidedError();
    }
  }

  private showNoCommunicationServiceProvidedError() {
    console.warn(titles.noCommunicationServiceProvidedError);
  }
}
